import "./styles.css";
import React from "react";

import Todo from "./todo/Todo";

const initialList = [
  {
    id: "a",
    firstname: "Robin",
    lastname: "Wieruch",
    isCompleted: "done",
    year: 1988
  },
  {
    id: "b",
    firstname: "Dave",
    lastname: "Davidds",
    isCompleted: "done",
    year: 1990
  }
];
export default function App() {
  const [list, setList] = React.useState(initialList);

  const handleRemove = (id) => {
    const newList = list.filter((item) => item.id !== id);
    console.log(newList);
    setList(newList);
  };
  const doneAll = (id) => {
    const newList2 = list.filter((item) => item.id === id);
    setList(newList2);
    console.log(setList(newList2));
  };

  const refreshAll = () => {
    const newList3 = initialList;
    setList(newList3);
  };
  return (
    <div className="App">
      <Todo
        list={list}
        onRemove={handleRemove}
        doneAll={doneAll}
        refreshAll={refreshAll}
      />
    </div>
  );
}
